@import '../../utils/mixins.scss';
@import '../../utils//variables.scss';
@import '../../utils/fonts.scss';

* {
  box-sizing: border-box;
  text-decoration: none;
}

.footer {
  position: fixed;
  bottom: 0;
  max-height: 259px;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 32px;
  padding: 32px 16px;
  font-family: 'Mont', sans-serif;

  &__logo {
    margin-right: auto;
    align-items: flex-start;
  }

  &__links {
    font-size: 12px;
    gap: 16px 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: column;
    text-transform: uppercase;
    &-link {
      color: $color-secondary;
      font-weight: 800;
      letter-spacing: 1px;
    }
  }

  &__return {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;
    color: $color-secondary;
    font-weight: 700;

    :hover {
      background-image: url('../../../public/img/vector-white.png');
      background-color: $color-primary;
      transition: all 0.3s ease-in-out;
    }

    &-button {
      width: 32px;
      height: 32px;
      border: 1px solid $color-secondary;
      background-image: url('../../../public/img/vector.png');
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  @include for-desktop() {
    align-items: center;
    flex-direction: row;
    &__logo,
    &__links,
    &__return {
      flex: 1;
    }
    &__links {
      flex-direction: row;
    }
  }

  @include for-tablet() {
    align-items: center;
    flex-direction: row;
    &__logo,
    &__links,
    &__return {
      flex: 1;
    }
    &__logo {
      margin-right: 0;
    }
    &__links {
      flex-direction: row;
      gap: 13.5px;
    }
  }
}
